.App {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
}

.type-faster {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.count {
  color: var(--green);
  font-size: 60px;
}

.timer {
  color: var(--purple);
  text-transform: none;
}

button {
  background-color: transparent;
  border: 1px solid var(--blue);
  color: var(--blue);
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  display: inline-block;
  font-size: 16px;
}

button:hover {
  outline: 3px solid var(--blue);
  font-weight: 600;
}



